import React, {useEffect, useState} from "react";
import AnimationRevealPage from "helpers/AnimationRevealPage.js";
import { Container as ContainerBase } from "components/misc/Layouts";
import tw from "twin.macro";
import styled from "styled-components";
import illustration from "images/login-illustration.svg";
import ragingsouls from "images/ragingsouls.jpg";
import logo from "images/logo.svg";
import newYellowLogo from "images/newyellowlogo.jpg"
import googleIconImageSrc from "images/google-icon.png";
import twitterIconImageSrc from "images/twitter-icon.png";
import { ReactComponent as LoginIcon } from "feather-icons/dist/icons/log-in.svg";
import {Link, useNavigate} from 'react-router-dom';

import {DOMAIN} from "../constants";
import {PrimaryButton as PrimaryButtonBase} from "../components/misc/Buttons";

const Container = tw(ContainerBase)`min-h-screen bg-yellow-500 text-white font-medium flex justify-center -m-8`;
const Content = tw.div`max-w-screen-xl m-0 sm:mx-20 sm:my-16 bg-white text-gray-900 shadow sm:rounded-lg flex justify-center flex-1`;
const MainContainer = tw.div`lg:w-1/2 xl:w-5/12 p-6 sm:p-12`;
const LogoLink = tw.a``;
const LogoImage = tw.img`h-12 mx-auto`;
const MainContent = tw.div`mt-12 flex flex-col items-center`;
const Heading = tw.h1`text-2xl xl:text-3xl font-extrabold`;
const FormContainer = tw.div`w-full flex-1 mt-8`;

const SocialButtonsContainer = tw.div`flex flex-col items-center`;
const SocialButton = styled.a`
  ${tw`w-full max-w-xs font-semibold rounded-lg py-3 border text-gray-900 bg-gray-100 hocus:bg-gray-200 hocus:border-gray-400 flex items-center justify-center transition-all duration-300 focus:outline-none focus:shadow-outline text-sm mt-5 first:mt-0`}
  .iconContainer {
    ${tw`bg-white p-2 rounded-full`}
  }
  .icon {
    ${tw`w-4`}
  }
  .text {
    ${tw`ml-4`}
  }
`;
const Text = tw(PrimaryButtonBase)`w-full mt-8`;

const Form = tw.form`mx-auto max-w-xs`;
const Input = tw.input`w-full px-8 py-4 rounded-lg font-medium bg-gray-100 border border-gray-200 placeholder-gray-500 text-sm focus:outline-none focus:border-gray-400 focus:bg-white mt-5 first:mt-0`;
const SubmitButton = styled.button`
  ${tw`mt-5 tracking-wide font-semibold bg-primary-500 text-gray-100 w-full py-4 rounded-lg hover:bg-primary-900 transition-all duration-300 ease-in-out flex items-center justify-center focus:shadow-outline focus:outline-none`}
  .icon {
    ${tw`w-6 h-6 -ml-2`}
  }
  .text {
    ${tw`ml-3`}
  }
`;

const port =3005

const IllustrationContainer = tw.div`sm:rounded-r-lg flex-1 bg-yellow-100 text-center hidden lg:flex justify-center`;
const IllustrationImage = styled.div`
  ${props => `background-image: url("${props.imageSrc}");`}
  ${tw`m-12 xl:m-16 w-full max-w-sm bg-contain bg-center bg-no-repeat`}
`;

function Login(){
  let logoLinkUrl = "#";
  let illustrationImageSrc = ragingsouls;
  let headingText = "Sign In To My website";
  let socialButtons = [
    {
      iconImageSrc: googleIconImageSrc,
      text: "Sign In With Google",
      url: "https://google.com"
    },
    {
      iconImageSrc: twitterIconImageSrc,
      text: "Sign In With Twitter",
      url: "https://twitter.com"
    }
  ];
  let submitButtonText = "Sign In";
  let SubmitButtonIcon = LoginIcon;
  let forgotPasswordUrl = "#";
  let signupUrl = "#";

  const navigate = useNavigate();
  const [email,setEmail] = useState('')
  const [password,setPassword] = useState('')
  const [newRoute, setNewRoute] = useState(null);
  const [isSet,setIsSet]=useState(false);
  const handleSubmit = (event)=>{
      event.preventDefault()


    console.log(JSON.stringify({email,password}));

    fetch(`${DOMAIN}/api/users/login`,{
      method:'POST',
      body: JSON.stringify({email,password}),
      headers:{
        'Content-Type':'application/json'
      }
    })
        .then((response) => {
          if (response.status === 200) {
            return response.json(); // This returns a Promise
          } else {
            console.log('Request failed with status ' + response.status);
            throw new Error('Request failed with status ' + response.status);
          }
        })
        .then((data) => {
          console.log(data);
          console.log(JSON.stringify(data));
          setNewRoute(`/DeveloperPage/${data.email}`);
          setIsSet(true);


        })
        .catch((error) => {
          console.log(error);
        });
  }
  // useEffect(() => {
  //   if (newRoute) {
  //     navigate(newRoute);
  //   }
  // }, [newRoute, navigate]);
  return <AnimationRevealPage>
    <Container>
      <Content>
        <MainContainer>
          <LogoLink href={logoLinkUrl}>
            <LogoImage src={newYellowLogo}/>
          </LogoLink>
          <MainContent>
            <Heading>{headingText}</Heading>
            <FormContainer>
              <Form onSubmit={handleSubmit}>
                <Input type="email" placeholder="Email" name={'email'} value={email} onChange={(e)=> setEmail(e.target.value)}/>
                <Input type="password" placeholder="Password" name={'password'} value={password} onChange={(e)=> setPassword(e.target.value)}/>
                <SubmitButton type="submit">
                  <SubmitButtonIcon className="icon"/>
                  <span className="text">{submitButtonText}</span>
                </SubmitButton>
              </Form>
              {/*<p tw="mt-6 text-xs text-gray-600 text-center">*/}
              {/*  <a href={forgotPasswordUrl} tw="border-b border-gray-500 border-dotted">*/}
              {/*    Forgot Password ?*/}
              {/*  </a>*/}
              {/*</p>*/}
              {/*<p tw="mt-8 text-sm text-gray-600 text-center">*/}
              {/*  Dont have an account?{" "}*/}
              {/*  <a href={signupUrl} tw="border-b border-gray-500 border-dotted">*/}
              {/*    Sign Up*/}
              {/*  </a>*/}
              {/*</p>*/}
              {isSet &&(
                  <Link to= {newRoute} >
                    <Text>Move to Developer page</Text>
                  </Link>
              )}
            </FormContainer>
          </MainContent>
        </MainContainer>
        <IllustrationContainer>
          <IllustrationImage imageSrc={illustrationImageSrc}/>
        </IllustrationContainer>
      </Content>
    </Container>
  </AnimationRevealPage>
};
export default Login;
